'use client'

import { useState } from 'react'
import LineCard from '@/components/line-card'

interface Line {
  id: string
  number: string
  name: string
  status: 'active' | 'inactive'
  stopsCount: number
  firstStop: string
  lastStop: string
}

const mockLines: Line[] = [
  {
    id: '1',
    number: 'L01',
    name: 'Agadir Centre Ville',
    status: 'active',
    stopsCount: 5,
    firstStop: 'Gare Routière',
    lastStop: 'Tiklouine',
  },
  {
    id: '2',
    number: 'L02',
    name: 'Agadir Plage',
    status: 'active',
    stopsCount: 8,
    firstStop: 'Centre',
    lastStop: 'Plage d\'Agadir',
  },
  {
    id: '3',
    number: 'L03',
    name: 'Agadir Aït Melloul',
    status: 'inactive',
    stopsCount: 6,
    firstStop: 'Agadir',
    lastStop: 'Aït Melloul',
  },
  {
    id: '4',
    number: 'L04',
    name: 'Service Nocturne',
    status: 'active',
    stopsCount: 10,
    firstStop: 'Gare Routière',
    lastStop: 'Zones Résidentielles',
  },
  {
    id: '5',
    number: 'L05',
    name: 'Agadir - Tiznit',
    status: 'active',
    stopsCount: 18,
    firstStop: 'Agadir Gare',
    lastStop: 'Tiznit Centre',
  },
  {
    id: '6',
    number: 'L06',
    name: 'Quartiers Périphériques',
    status: 'inactive',
    stopsCount: 9,
    firstStop: 'Centre Ville',
    lastStop: 'Banlieue Est',
  },
]

interface LinePageProps {
  selectedCity: string
}

export default function LinesPage({ selectedCity }: LinePageProps) {
  const [lines, setLines] = useState<Line[]>(mockLines)

  const toggleLineStatus = (id: string) => {
    setLines(
      lines.map((line) =>
        line.id === id
          ? { ...line, status: line.status === 'active' ? 'inactive' : 'active' }
          : line
      )
    )
  }

  return (
    <div className="ml-80 p-8">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {lines.map((line) => (
          <LineCard
            key={line.id}
            line={line}
            onToggleStatus={() => toggleLineStatus(line.id)}
          />
        ))}
      </div>
    </div>
  )
}
