'use client'

import { useState } from 'react'
import { Search } from 'lucide-react'
import StopsTable from '@/components/stops-table'

interface Stop {
  id: string
  name: string
  code: string
  address: string
  latitude: number
  longitude: number
  pmr: boolean
  status: 'active' | 'inactive'
}

const mockStops: Stop[] = [
  {
    id: '1',
    name: 'Gare Routière Agadir',
    code: 'GA-01',
    address: 'Avenue Mohamed V, Agadir',
    latitude: 30.42,
    longitude: -9.56,
    pmr: true,
    status: 'active',
  },
  {
    id: '2',
    name: 'Plage d\'Agadir',
    code: 'GA-02',
    address: 'Boulevard de la Plage, Agadir',
    latitude: 30.43,
    longitude: -9.58,
    pmr: true,
    status: 'active',
  },
  {
    id: '3',
    name: 'Centre Commercial Souss Massa',
    code: 'GA-03',
    address: 'Route de Tiznit, Agadir',
    latitude: 30.41,
    longitude: -9.60,
    pmr: false,
    status: 'inactive',
  },
  {
    id: '4',
    name: 'Hôpital Argane',
    code: 'GA-04',
    address: 'Boulevard Tata, Agadir',
    latitude: 30.44,
    longitude: -9.57,
    pmr: true,
    status: 'active',
  },
  {
    id: '5',
    name: 'Université Ibn Zohr',
    code: 'GA-05',
    address: 'Campus Universitaire, Agadir',
    latitude: 30.40,
    longitude: -9.55,
    pmr: false,
    status: 'active',
  },
  {
    id: '6',
    name: 'Aït Melloul Marché',
    code: 'AM-01',
    address: 'Marché Central, Aït Melloul',
    latitude: 30.35,
    longitude: -9.65,
    pmr: false,
    status: 'active',
  },
]

interface StopsPageProps {
  selectedCity: string
}

export default function StopsPage({ selectedCity }: StopsPageProps) {
  const [stops, setStops] = useState<Stop[]>(mockStops)
  const [searchQuery, setSearchQuery] = useState('')

  const filteredStops = stops.filter(
    (stop) =>
      stop.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      stop.code.toLowerCase().includes(searchQuery.toLowerCase())
  )

  const toggleStopStatus = (id: string) => {
    setStops(
      stops.map((stop) =>
        stop.id === id
          ? { ...stop, status: stop.status === 'active' ? 'inactive' : 'active' }
          : stop
      )
    )
  }

  return (
    <div className="ml-64">
      <div className="mb-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Liste des Arrêts</h3>

        {/* Search Bar */}
        <div className="mb-4 relative">
          <Search className="absolute left-3 top-3 text-gray-400" size={20} />
          <input
            type="text"
            placeholder="Rechercher par nom ou code..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg bg-white text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        {/* Table */}
        <StopsTable stops={filteredStops} onToggleStatus={toggleStopStatus} />
      </div>
    </div>
  )
}
